:root {
  --main-black-color: #141414;
  --main-white-color: #fff;
  --dark-green-color: #1e2827;
  --numeral-gray-color: rgba(20, 20, 20, .4);
  --text-gray-color: rgba(20, 20, 20, .5);
  color: #00f;
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  font-family: DM Sans, sans-serif;
  font-size: 14px;
}



.all_categories, section {
  height: 228px;
  width: 375px;
  background: #fff;
  margin: 40px 20px;
}

.category_item {
  letter-spacing: -.04em;
  text-align: left;
  color: rgba(17, 17, 17, .6);
  cursor: pointer;
  white-space: nowrap;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.125;
  transition: color .5s cubic-bezier(.25, .46, .45, .94);
}

.category_item:hover {
  text-transform: uppercase;
  color: #4f2ee8;
  font-family: DM Sans;
  font-weight: 700;
}

.category_list {
  min-width: 100%;
  height: 100%;
  border-radius: 3px;
  flex-direction: column;
  justify-content: flex-start;
  align-items: baseline;
  gap: 24px;
  margin: 0;
  padding: 0;
  display: inline-flex;
  overflow-y: auto;
}

.category_list::-webkit-scrollbar {
  width: 8px;
}

.category_list::-webkit-scrollbar-thumb {
  height: 114px;
  background-color: #e0e0e0;
  border-radius: 12px;
}

@media screen and (min-width: 768px) and (max-width: 1439px) {
  .all_categories, section {
    height: 472px;
    width: 337px;
    margin: 40px 32px 0;
  }

  .category_item {
    font-size: 18px;
    line-height: 1.33333;
  }

  .category_list {
    gap: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .all_categories, section {
    height: 472px;
    width: 356px;
    margin: 40px 40px 86px 24px;
  }

  .category_item {
    font-size: 18px;
    line-height: 1.33333;
  }

  .category_list {
    gap: 32px;
  }
}









